import { Component, cloneElement } from "react";

import isSameDay from "date-fns/is_same_day";
import isWeekend from "date-fns/is_weekend";

import Head from "./Head";
import Calendar from "./Calendar";
import $ from "./style.css";

const specialDates = [
	{
		type: "holiday",
		date: "2018-05-21",
		name: "elena",
	},
	{
		type: "holiday",
		date: "2018-06-21",
		name: "test",
	},
	{
		type: "event",
		date: "2018-05-29",
		name: "event",
	},
	{
		type: "event",
		date: "2018-05-21",
		name: "event",
	},
	{
		type: "reminder",
		date: "2018-05-21",
		name: "next",
	},
];

var currentDate = new Date("2018-05-21");

const SpecialDay = ({ date }) => (
	<div>
		{specialDates.map((specialDate, key) => {
			if (isSameDay(date, new Date(specialDate.date))) {
				return (
					<div
						key={key}
						style={{
							width: "100%",
							marginTop: "0.25rem",
							border: `2px solid ${
								specialDate.type == "holiday"
									? "red"
									: specialDate.type == "event"
										? "green"
										: specialDate.type == "reminder"
											? "orange"
											: "transparent"
							}`,
						}}
					/>
				);
			}
		})}
	</div>
);

const DisplaySpecialDay = ({ date }) => (
	<div>
		{specialDates.map((specialDate, key) => {
			if (isSameDay(date, new Date(specialDate.date))) {
				return (
					<div
						key={key}
						style={{
							color: `${
								specialDate.type == "holiday"
									? "red"
									: specialDate.type == "event"
										? "green"
										: specialDate.type == "reminder"
											? "orange"
											: "transparent"
							}`,
						}}
					>
						{specialDate.name}
					</div>
				);
			}
		})}
	</div>
);

function onRender({ dayNumber, date }, cell) {
	const content =
		dayNumber === 12 ? (
			<div>dev</div>
		) : (
			<div>
				{dayNumber}
				<SpecialDay date={date} />
			</div>
		);

	const isWeekendDay = isWeekend(date);

	const styles = {
		backgroundColor: isWeekendDay ? "rgba(0,0,0,0.1)" : "",
		padding: "0.75rem 0",
	};

	if (cell != null)
		return cloneElement(
			cell,
			{
				style: styles,
				onClick: () => {
					currentDate = date;
				},
			},
			content
		);
	return content;
}

function selected({ date, dayNumber }, cell) {
	const styles = {
		borderColor: "yellow",
		padding: "0.75rem 0",
	};
	if (cell != null)
		return cloneElement(
			cell,
			{
				style: styles,
			},
			<div>
				{dayNumber}
				<SpecialDay date={date} />
			</div>
		);
	return content;
}

export default class Application extends Component {
	state = {
		date: "2018-05-21",
	};
	render() {
		const { date } = this.state;
		return (
			<div>
				<Head />
				<Calendar
					date={date}
					onChange={onChange}
					onRender={onRender}
					selected={selected}
				/>
				<DisplaySpecialDay date={currentDate} />
			</div>
		);
	}
}



